@use '_colors';

.search { 
    position: relative;
    display: flex;
    align-items: center;    
    

    .icon { 
        position: absolute;
        left: 1.2rem;
        height: 1.8rem;
        width: 1.8rem;
        color: black;     
    }

    .input { 
        width: 40rem;
        height: 2.5rem;
        border-radius: 0.3rem;
        padding: 0 4.5rem;
        border: 0;        
        font-size: 1.4rem; 
        color: black;       

        @media screen and (max-width: 1000px) {      
            width: 14rem;
        }  
    }    
}
